<template>
  <div id="app-autocomplete">
    <input
      type="text"
      autocomplete="off"
      placeholder="Add Country..."
      v-model="userCountry"
      @input="onInput"
    />
    <button @click.stop="onClear">
      <font-awesome-icon icon="times" />
    </button>
    <ul v-show="isOpen" class="elevated">
      <li
        v-for="(country, index) of menuCountries"
        :key="index"
        @click.stop="onSelect(country)"
      >{{ country }}</li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "app-autocomplete",
  props: {
    worldCountries: {
      type: Array,
      default() {
        return [];
      }
    }
  },
  data() {
    return {
      userCountry: "",
      menuCountries: []
    };
  },
  computed: {
    isOpen() {
      return this.userCountry ? true : false;
    }
  },
  methods: {
    onInput() {
      this.menuCountries = this.worldCountries.filter(country =>
        country.startsWith(this.userCountry.toLowerCase())
      );
    },
    onSelect(country) {
      this.userCountry = "";
      this.$emit("select", country);
    },
    onClear() {
      this.userCountry = "";
    }
  }
};
</script>

<style lang="scss">
#app-autocomplete {
  // position: fixed;
  // top: 1rem;
  // left: 50%;
  // transform: translateX(-50%);
  // width: calc(100% - 2rem);
  position: relative;
  margin: 1rem 0;

  input {
    outline: none;
    border: none;
    width: 100%;
    height: 36px;
    padding: 0 1rem;
    border-radius: 4px;
  }

  button {
    cursor: pointer;
    position: absolute;
    height: 30px;
    border: none;
    width: 30px;
    top: 50%;
    left: calc(100% - 30px - 0.5rem);
    transform: translateY(-50%);
    background: transparent;
    outline: none;
  }

  ul {
    background: #fff;
    max-height: 320px;
    list-style: none;
    overflow-y: scroll;
    position: absolute;
    border-radius: 4px;
    width: 100%;
    padding: 0;
    margin: 0;
    top: 40px;

    li {
      cursor: pointer;
      padding: 1rem;
      font-weight: 500;
      text-transform: capitalize;
      transition: background-color 0.5s;

      &:hover {
        background-color: lighten($color: #000, $amount: 90);
      }
    }
  }
}
</style>
