<template>
  <div id="app-statistics">
    <template v-if="statistics">
      <div class="header">
        <div class="title">{{ statistics.name }}</div>
        <div class="controls">
          <button
            class="btn"
            @click.stop="isFavourite ? onRemoveBookmark(statistics.name) : onAddBookmark(statistics.name)"
          >
            <font-awesome-icon :class="isFavourite ? '' : 'unselected'" icon="thumbtack" />
          </button>
          <button class="btn" @click.stop="onDeleteItem(statistics.name)">
            <font-awesome-icon icon="times" />
          </button>
        </div>
      </div>
      <div class="summary">
        <div class="stats-item">
          <small class="stat-item-category">Cases</small>
          <div class="stat-item-number">{{ statistics.cases.total }}</div>
          <div>
            <small>Today: {{ statistics.cases.today }} ({{ statistics.cases.growth | toPercentage }})</small>
          </div>
        </div>
        <div class="stats-item">
          <small class="stat-item-category">Deaths</small>
          <div class="stat-item-number">{{ statistics.deaths.total }}</div>
          <div>
            <small>Today: {{ statistics.deaths.today}} ({{ statistics.deaths.growth | toPercentage }})</small>
          </div>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  name: "app-statistics",

  computed: {
    ...mapGetters(["storeBookmarks"]),

    isFavourite() {
      return this.storeBookmarks.includes(this.statistics.name.toLowerCase());
    }
  },

  props: {
    statistics: {
      type: Object,
      default() {
        return [];
      }
    }
  },

  methods: {
    onAddBookmark(country) {
      this.$store
        .dispatch("addBookmark", country.trim().toLowerCase())
        .then(() => {
          this.$emit("notify", `${country} added to Favourites`);
        });
    },

    onRemoveBookmark(country) {
      this.$store
        .dispatch("removeBookmark", country.trim().toLowerCase())
        .then(() => {
          this.$emit("notify", `${country} removed from Favourites`);
        });
    },

    onDeleteItem(country) {
      this.$store.dispatch("removeData", country.trim().toLowerCase());
    }
  },

  filters: {
    toPercentage(value) {
      const symbol = value > 0 ? "+" : "-";
      return `${symbol}${Math.abs(value).toFixed(1)}%`;
    }
  }
};
</script>

<style lang="scss">
#app-statistics {
  padding: 1rem;
  background: rgb(173, 14, 93);
  border-radius: 12px;
  margin-bottom: 0.5rem;
  color: #fff;

  .header {
    margin-bottom: 1em;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .title {
      font-weight: bold;
      font-size: 16px;
    }

    .controls {
      display: inline-flex;
      align-items: center;
    }
  }

  .summary {
    display: grid;
    grid-column-gap: 0.25rem;
    grid-template-columns: 0.5fr 0.5fr;
    margin-bottom: 1rem;
    font-size: 12px;

    .stats-item {
      text-align: center;
      background: lighten(rgb(173, 14, 93), 5);
      padding: 1rem 0;

      .stat-item-number {
        font-size: 26px;
        font-weight: 100;
      }
    }
  }

  .info {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    text-align: center;
    font-size: 12px;
    color: lighten(rgb(173, 14, 93), 50);
    margin-bottom: 1rem;
  }

  .btn {
    background: rgb(173, 14, 93);
    border: none;
    color: #fff;
    width: 30px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    margin: 0 0.25rem;
    outline: none;

    .unselected {
      color: rgba(255, 255, 255, 0.25);
    }
  }
}
</style>
