import Vue from "vue";
import Vuex from "vuex";
import types from "./mutations";
import Storage from "../Storage";
import Service from "../Service";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    bookmarks: [],
    userData: [],
    userOptions: [],
  },

  getters: {
    storeBookmarks: (state) => {
      return state.bookmarks;
    },

    storeUserData: (state) => {
      return state.userData;
    },

    storeUserOptions: (state) => {
      return state.userOptions.map((option) => option.toLowerCase());
    },
  },

  mutations: {
    [types.BOOKMARKS_LOAD](state, payload) {
      Vue.set(state, "bookmarks", payload.bookmarks);
    },
    [types.USERDATA_LOAD](state, payload) {
      Vue.set(state, "userData", payload.data);
    },
    [types.USEROPTIONS_LOAD](state, payload) {
      Vue.set(state, "userOptions", payload.options);
    },
  },

  actions: {
    // FIXME
    // all the bookmark stuff can go in a module
    loadBookmarks: async ({ commit }) => {
      try {
        const bookmarks = await Storage.async_getItems();
        commit({ type: "BOOKMARKS_LOAD", bookmarks: bookmarks || [] });
      } catch (error) {
        throw new Error(`Error (loadBookmarks): ${error}`);
      }
    },

    addBookmark: async ({ getters, commit }, country) => {
      if (!getters.storeBookmarks.includes(country)) {
        const bookmarks = [...getters.storeBookmarks, country];
        try {
          await Storage.async_setItems(bookmarks);
          commit({ type: "BOOKMARKS_LOAD", bookmarks: bookmarks });
        } catch (error) {
          throw new Error(`Error (addBookmarks): ${error}`);
        }
      }
    },

    removeBookmark: async ({ getters, commit }, country) => {
      if (getters.storeBookmarks.includes(country)) {
        const bookmarks = getters.storeBookmarks.filter((item) => item !== country);
        try {
          await Storage.async_setItems(bookmarks);
          commit({ type: "BOOKMARKS_LOAD", bookmarks: bookmarks });
        } catch (error) {
          throw new Error(`Error (removeBookmarks): ${error}`);
        }
      }
    },

    loadOptions: async ({ commit }) => {
      try {
        const { data } = await Service.getOptions();
        commit({ type: "USEROPTIONS_LOAD", options: data.options });
      } catch (error) {
        throw new Error(`Error (loadOptions): ${error}`);
      }
    },

    loadData: async ({ commit }, countries = []) => {
      if (countries.length > 0) {
        try {
          const { data } = await Service.getCountries(countries);
          commit({ type: "USERDATA_LOAD", data: data.countries });
        } catch (error) {
          throw new Error(`Error (loadData): ${error}`);
        }
      }
    },

    addData: async ({ state, commit }, country = "") => {
      try {
        const { data } = await Service.getCountry(country);
        commit({ type: "USERDATA_LOAD", data: [...state.userData, data.country] });
      } catch (error) {
        throw new Error(`Error (addData): ${error}`);
      }
    },

    removeData: ({ state, commit }, country) => {
      const userData = [...state.userData].filter((item) => {
        return item.name.toLowerCase() !== country.trim().toLowerCase();
      });
      commit({ type: "USERDATA_LOAD", data: userData });
    },

    initialize: async ({ getters, dispatch }) => {
      try {
        await dispatch("loadBookmarks");
        await dispatch("loadOptions");
        await dispatch("loadData", getters.storeBookmarks);
      } catch (error) {
        throw new Error(`Error (initialize): ${error}`);
      }
    },
  },
  modules: {},
});
